
'use server';

interface SchoolResult {
    place_id: string;
    name: string;
    vicinity: string;
    geometry: {
        location: {
            lat: number;
            lng: number;
        }
    }
}

export async function findNearbySchools(
  location: { lat: number, lng: number }
): Promise<{ success: boolean; data?: any[], error?: string }> {
  const apiKey = process.env.GOOGLE_MAPS_API_KEY;
  if (!apiKey) {
    return { success: false, error: "Google Maps API key is not configured on the server." };
  }
  
  const url = new URL('https://maps.googleapis.com/maps/api/place/nearbysearch/json');
  url.searchParams.append('location', `${location.lat},${location.lng}`);
  url.searchParams.append('radius', '5000'); // 5km radius
  url.searchParams.append('type', 'school');
  url.searchParams.append('key', apiKey);

  try {
    const response = await fetch(url.toString());
    const data = await response.json();

    if (data.status !== 'OK') {
        return { success: false, error: `Places API error: ${data.status} - ${data.error_message || 'No results'}` };
    }

    const schools = data.results.map((school: SchoolResult) => ({
      place_id: school.place_id,
      name: school.name,
      vicinity: school.vicinity,
      location: school.geometry.location,
    }));

    return { success: true, data: schools };
  } catch (e) {
    console.error("Error fetching nearby schools:", e);
    return { success: false, error: 'An unknown error occurred while fetching schools.' };
  }
}
